@keyframes dzSpeed {
  0% {
    border-radius: 50%;
    transform: translate(0%); }
  25% {
    border-radius: 0;
    transform: translate(150%) scale(0.5); }
  50% {
    border-radius: 50%;
    transform: translate(150%, 150%); }
  75% {
    border-radius: 0;
