@keyframes dotScale {
  0% {
    transform: scale(0);
    opacity: 0; }
  100% {
    transform: scale(1);
    opacity: 1; } }

@keyframes HeartUpToDown {
  0% {
    transform: translateY(-35px);
    opacity: 0; }
  25% {
    opacity: 0.4; }
  50% {
    opacity: 0.6; }
  75% {
    opacity: 0.8; }
  100% {
