// video-bx
.video-bx1{
	position: relative;
	min-width:170px;
	width:170px;
	height:170px;
	border-radius:var(--border-radius-base);
	overflow:hidden;
	img{
		height:100%;
		width:100%;
		object-fit:cover;
	}
	.video-btn {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		text-align: center;
		.popup-youtube{
			display:inline-block;
			height:60px;
			width:60px;
			min-width:60px;
			line-height:60px;
			font-size: 20px;
			border-radius:60px;
			background-color:#fff;
			color:var(--primary);
			&:before{
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				display: block;
				border: 1px solid #fff;
				-webkit-border-radius: 50%;
				-khtml-border-radius: 50%;
				-moz-border-radius: 50%;
				-ms-border-radius: 50%;
				-o-border-radius: 50%;
				border-radius: 100%;
				animation: animationSignal1;
				animation-iteration-count: infinite;
				animation-duration: 3s;
				-webkit-animation: animationSignal1;
				-webkit-animation-iteration-count: infinite;
				-webkit-animation-duration: 3s;
				z-index: -1;
				transform:  scale(1);
				-moz-transform:  scale(1);
				-webkit-transform:  scale(1);
				-ms-transform:  scale(1);
				-o-transform:  scale(1);
				-moz-transition:all 0.5;
				-o-transition:all 0.5;
				-webkit-transition:all 0.5;
				-ms-transition:all 0.5;
				transition:all 0.5;
			}
			&:after{
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				display: block;
				border: 1px solid #fff;
				-webkit-border-radius: 100%;
				-khtml-border-radius: 100%;
				-moz-border-radius: 100%;
				-ms-border-radius: 100%;
				-o-border-radius: 100%;
				border-radius: 100%;
				animation: animationSignal2;
				animation-iteration-count: infinite;
				animation-duration: 3s;
				-webkit-animation: animationSignal2;
				-webkit-animation-iteration-count: infinite;
				-webkit-animation-duration: 3s;
				z-index: -1;
				transform:  scale(1);
				-moz-transform:  scale(1);
				-webkit-transform:  scale(1);
				-ms-transform:  scale(1);
				-o-transform:  scale(1);
				-moz-transition:all 0.5;
				-o-transition:all 0.5;
				-webkit-transition:all 0.5;
				-ms-transition:all 0.5;
				transition:all 0.5;
			}
		}
	}
	@include respond ('phone'){
		height:120px;
		width:120px;
		min-width:120px;
		.video-btn {
			.popup-youtube{
				height:40px;
				width:40px;
				min-width:40px;
				line-height:40px;
				font-size:16px;
			}
		}
	}
}
